@media screen and (max-width: 1080px) {
	* {
		font-size: 45px;
		.gridContainer {
			main {
				.mainDescription {
					display: none; }
				.mainDescriptionMobile {
					display: flex;
					flex-direction: column;
					margin-bottom: 20px; }
				.mainImg {
					display: none; }
				.mainImgMobile {
					display: flex;
					justify-content: center;
					width: 100%;
					position: relative;
					img {
						max-width: 100%; }
					.resize {
						position: absolute;
						top: 0;
						left: 0;
						font-size: 25px;
						border: 5px solid #009AD0;
						background: #009AD0;
						opacity: 0.2;
						a {
							color: white;
							&:visited {
								color: white; } } } }
				.mainColomns {
					grid-template-columns: 1fr;
					grid-template-rows: repeat(2,1fr);
					.mainColomnsLeft {
						.mainColomnsBottom {
							display: none; }
						.mainColomnsBottomMobile {
							display: flex;
							flex-direction: column;
							margin-bottom: 20px; } }
					.mainColomnsRight {
						.mainColomnsBottom {
							display: none; }
						.mainColomnsBottomMobile {
							display: flex;
							flex-direction: column; } } }
				section.price {
					p {
						white-space: normal; } }
				section.signUp {
					h1 {
						white-space: normal; } }
				.contacts {
					.imgContainer {
						img {
							height: 80px; } } } } } } }
@media screen and (max-width: 945px) {
	.gridContainer {
		main {
			section.price {
				p {
					white-space: normal; } }
			section.signUp {
				h1 {
					white-space: normal; } } } } }
